.navbar{
    height: 5rem;
    width: 100vw;
    margin: 0 auto ;
    max-width: 75rem;
    padding: 1rem 2rem ;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 3;
    background-color: rgb(30, 30, 30);
}

.logo{
    object-fit: cover;
    height: 3rem;
    width: 4rem;
}
.destopMenuListItem{
    color: white;
    margin: 1rem;
    cursor: pointer;
}
.destopMenuListItem:hover{
    color: yellow;
    padding: 0.5rem;
    border-bottom: 3px solid yellow;
}

.destopMenuBtn{
    background: white;
    color: black;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 1rem;
    border-radius: 2rem;
}
.destopMenuImg{
    object-fit: cover;
    height: 1rem;
    width: 1rem;
    margin: 1rem;
}
.active{
    color: yellow;
padding-bottom: 0.5rem;
}
.navmenu{
    color: white;
    position: absolute;
    top: 4rem;
    right: 2rem;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding: 0.5rem;
    height: fit-content;
    min-width: 15rem;
    background-color: rgb(40,40,40);
    border-radius: 1rem;
}
.ListItem{
    color: white;
    padding: 0.5rem  3rem;
    margin: 0.25rem ;
    background-color: rgb(30,30,30);
}
.mobmenu{
    display: none;
    object-fit: cover;
    height: 1.8rem;
    width: 3rem;
}
@media  screen and (max-width:720px) and (min-width:300px) {
    .mobmenu{
        display: flex;
        margin-right: 40px;
    }
    .destopMenu{
        display: none;
    }
    .destopMenuBtn{
        display: none;
    }
}